import React from 'react';
import { makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography'; 
import Ayuda from './Ayuda';
import Hidden from '@material-ui/core/Hidden';
import CssBaseline from '@material-ui/core/CssBaseline';

const useStyles = makeStyles ({
    encabezado: {
        height: '9vh',
        width: '100%',
        marginTop: '3.65rem',
        backgroundColor: '#333',
        position: 'fixed',
        display: 'flex',
    },
    titulo: {
          color: 'white',
          display: 'flex',
          alignItems: 'center',
          marginLeft: '3.6rem',
          marginRight: '57.5rem',
    },

    encabezadoCel: {
        height: '9vh',
        width: '100%',
        marginTop: '2.78rem',
        backgroundColor: '#333',
        position: 'fixed',
        display: 'flex',
    },
    tituloCel: {
          color: 'white',
          display: 'flex',
          alignItems: 'center',
          marginLeft: '1.4rem',
          marginRight: '5.4rem',
    },
    AyudaCel: {
        display: 'flex',
        alignItems: 'center',
    },
})

const AsistenteCompra = () => {
    const classes = useStyles();

    return (
        <div>
        <Hidden only={['xs', 'sm']}>
            <div className={classes.encabezado}>
                <Typography variant="subtitle1" className={classes.titulo}>
                    Asistente de compra
                </Typography>
                <Ayuda/>
            </div>
        </Hidden>

        {/* Pantalla adaptada a Celular XS */}
        <Hidden only={['sm', 'md', 'lg', 'xl']}>
        <CssBaseline />
            <div className={classes.encabezadoCel}>
                <Typography variant="body2" className={classes.tituloCel}>
                    Asistente de compra
                </Typography>
                <div className={classes.AyudaCel}>
                    <Ayuda/>
                </div>
            </div>
        </Hidden>
        </div>
    );
}

export default AsistenteCompra;