import React, { useCallback } from "react";
import { withRouter } from "react-router";
import {authentication} from "../firebase";
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Hidden from '@material-ui/core/Hidden';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright Â© '}
      <Link color="inherit" href="/comida-facil/">
        Comida FÃ¡cil - 
      </Link> Developed by: Alexis Carreras{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}
// backgroundColor: '#F1F0F0',

const useStyles = makeStyles((theme) => ({
  paper: {
    // marginTop: theme.spacing(8),
    paddingTop: '5rem',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(2),
  },
  submit: {
    margin: theme.spacing(1, 0, 2),
    backgroundColor: '#89C739',
    '&:hover': {
      backgroundColor: '#629F13',
    },
  },
  campo: {
    marginTop: '1rem',
  },
  footer: {
    marginTop: '4rem',
  },

  paperCel: {
    // marginTop: theme.spacing(8),
    paddingTop: '5.5rem',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    height: '25rem',
  },
  avatarCel: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  formCel: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(2),
  },
  submitCel: {
    margin: theme.spacing(2.45, 0, 2),
    backgroundColor: '#89C739',
    '&:hover': {
      backgroundColor: '#629F13',
    },
  },
  campoCel: {
    marginTop: '1rem',
    marginBottom: '0.35rem',
  },
  campo2Cel: {
    marginTop: '1.15rem',
    marginBottom: '0.3rem',
  },
  footerCel: {
    // marginTop: '4rem',
  },
}));

const Registar = ({ history }) => {
  const classes = useStyles();

  const handleSignUp = useCallback(async event => {
    event.preventDefault();
    const { email, password } = event.target.elements;
    try {
      await authentication
        .createUserWithEmailAndPassword(email.value, password.value);
      history.push("/comida-facil/");
    } catch (error) {
      alert(error);
    }
  }, [history]);

  return (
    <div>
    <Hidden only={['xs', 'sm']}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Crea tu cuenta!
          </Typography>
          <form onSubmit={handleSignUp} className={classes.form} noValidate>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label="Email"
                  name="email"
                  autoComplete="email"
                  className={classes.campo}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  variant="outlined"
                  required
                  fullWidth
                  name="password"
                  label="ContraseÃ±a"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                  className={classes.campo}
                />
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={<Checkbox value="allowExtraEmails" color="primary" />}
                  label="He leÃ­do y acepto los terminos de uso."
                  className={classes.campo}
                  required
                />
              </Grid>
            
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Crear cuenta
            </Button>
            <Grid container justify="flex-end">
              <Grid item>
                <Link href="/comida-facil/iniciar-sesion" variant="body2">
                  Â¿Ya tienes una cuenta? Inicia sesiÃ³n
                </Link>
              </Grid>
            </Grid>
          </form>
        </div>
        <Box mt={5} className={classes.footer}>
          <Copyright />
        </Box>
      </Container>
    </Hidden>

    {/* Pantalla adaptada a Celular XS */}
    <Hidden only={['sm', 'md', 'lg', 'xl']}>
    <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paperCel}>
          <Avatar className={classes.avatarCel}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Crea tu cuenta!
          </Typography>
          <form onSubmit={handleSignUp} className={classes.formCel} noValidate>
              <Grid item xs={12}>
                <TextField
                  size="small"
                  variant="outlined"
                  required
                  fullWidth
                  id="email"
                  label="Email"
                  name="email"
                  autoComplete="email"
                  className={classes.campoCel}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  size="small"
                  variant="outlined"
                  required
                  fullWidth
                  name="password"
                  label="ContraseÃ±a"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                  className={classes.campo2Cel}
                />
              </Grid>
              {/* <Grid item xs={12}>
                <FormControlLabel
                  control={<Checkbox value="allowExtraEmails" color="primary" />}
                  label="He leÃ­do y acepto los terminos de uso."
                  className={classes.campo}
                  required
                />
              </Grid> */}
            
            <Button
              size="small"
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submitCel}
            >
              Crear cuenta
            </Button>
            <Grid container justify="flex-end">
              <Grid item>
                <Link href="/comida-facil/iniciar-sesion" variant="body2">
                  Â¿Ya tienes una cuenta? Inicia sesiÃ³n
                </Link>
              </Grid>
            </Grid>
          </form>
        </div>
        <Box mt={5} className={classes.footerCel}>
          <Copyright />
        </Box>
      </Container>
    </Hidden>

    </div>
  );
}

export default withRouter(Registar); 